.personal-info {
  display: grid;

  grid-gap: 30px;
  grid-template-columns: 350px 1fr;
}

.personal-info__wrapper {
  margin-top: 50px;
  padding: 30px;

  background-color: $basic-white;
  border-radius: 20px;
  box-shadow: 0 4px 4px 0 rgba(0, 0, 0, 0.1);
}

.personal-info-photo__wrapper {
  display: flex;
  align-items: center;
}

.personal-info__header {
  font-weight: 700;
  font-size: 18px;
  font-family: "Oswald", "Arial", sans-serif;
}

.personal-info__text {
  margin-left: 30px;
}

.personal-info__balance {
  font-size: 14px;

  & span {
    font-weight: 800;
    font-size: 16px;
    font-family: "Oswald", "Arial", sans-serif;
  }
}

.personal-info__stars {
  margin-top: 20px;
  margin-bottom: 0;
  padding-left: 35px;

  font-size: 14px;

  background-image: url("../img/star.svg");
  background-repeat: no-repeat;
  background-position: left top;

  & span {
    font-weight: 800;
    font-size: 16px;
    font-family: "Oswald", "Arial", sans-serif;
  }
}

.personal-info__partners {
  margin-top: 20px;
  margin-bottom: 0;
  padding-left: 35px;

  font-size: 14px;

  background-image: url("../img/blue-star.svg");
  background-repeat: no-repeat;
  background-position: left top;

  & span {
    font-weight: 800;
    font-size: 16px;
    font-family: "Oswald", "Arial", sans-serif;
  }
}


.personal-info__status {
  display: flex;
  justify-content: space-between;
}

@media screen and (max-width: 991px) {
  .personal-info {
    grid-template-columns: 1fr;
  }

  .personal-info__status {
    justify-content: flex-start;
  }

  .personal-info__partners {
    margin-left: 40px;
  }
}
