.personal-info-news {
  display: grid;
  margin-top: 40px;
  margin-bottom: 80px;

  grid-gap: 30px;
  grid-template-columns: 350px 1fr;
}

.chat {
  position: relative;

  padding: 30px;
  padding-left: 20px;
  padding-right: 40px;

  background-color: $basic-white;
  border-radius: 20px;
  box-shadow: 0 4px 4px 0 rgba(0, 0, 0, 0.1);

  &::after {
    content: "";
    position: absolute;
    top: 5%;
    right: 10px;

    width: 8px;
    height: 90%;

    background-color: $basic-grey;
    border-radius: 10px;
  }

  &::before {
    content: "";
    position: absolute;
    top: 5%;
    right: 10px;
    z-index: 1;

    width: 8px;
    height: 10%;

    background-color: $special-grey;
    border-radius: 10px;
  }
}

.chat__title {
  margin-bottom: 20px;

  font-size: 14px;
  color: $special-black;
  text-transform: uppercase;
}

.chat__list {
  @include reset-list;
}

.chat__list-item {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 20px;

  &:last-child {
    margin-bottom: 0;
  }

  & textarea {
    width: 100%;
    min-height: 100px;
    border: 1px solid $special-grey;
    border-radius: 5px;
    padding-left: 10px;
    resize: none;

    &:focus {
      outline: none;
      opacity: 0.8;
    }
  }

  & button {
    border: none;
    background-color: $basic-yellow;
    border-radius: 20px;
    padding: 8px 20px;
    color: $basic-white;

    &:hover {
      opacity: 0.8;
    }

    &:focus {
      outline: none;
      opacity: 0.8;
    }
  }
}

.chat__list-item-header {
  order: 2;
  font-weight: 600;
  font-size: 16px;
  width: 100px;
  margin-top: 0;
}

.chat__list-item-wrapper {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  margin-left: 20px;
}

.chat__list-item-date {
  font-size: 12px;
  color: $special-black;
  opacity: 0.8;
  order: 1;
  align-self: flex-end;
  margin-bottom: 0;
}

.chat__list-item-text {
  margin-left: 20px;

  font-size: 14px;
}

@media screen and (max-width: 991px) {
  .personal-info-news {
    grid-template-columns: 1fr;
  }

  .chat {
    padding: 40px;
  }

  .chat__list-item {
    justify-content: flex-start;
  }

  .chat__list-item-img {
    width: 80px;
  }

  .chat__list-item-wrapper,
  .chat__list-item-text {
    margin-left: 30px;
  }

  .chat__list-item {
    & textarea {
      width: 100%;
    }
  }
}

@media screen and (max-width: 500px) {
  .chat__list-item-wrapper,
  .chat__list-item-text {
    margin-left: 20px;
  }

  .chat {
    padding: 20px;
    padding-right: 40px;
  }
}

@media screen and (max-width: 350px) {
  .chat__list-item-img {
    width: 100px;
  }

  .chat__list-item-wrapper,
  .chat__list-item-text {
    margin-left: 10px;
  }

  .chat__list-item-header {
    font-size: 14px;
  }

  .chat__list-item-date {
    font-size: 12px;
  }
}
